import { useSelector } from 'react-redux';
import { System } from '../basis/types/system';
import { TStore } from '../basis/types/tstore';
import Dark from '../basis/theme/dark';
import Light from '../basis/theme/light';
import '../basis/theme/colour'; // 测试注入彩色主题
import { NSTheme } from '../basis/types/theme';
import { getDesk } from '../utils/system';

// 系统网络状态
export const useSystem = () => {
  const status = useSelector((state: TStore.State) => state.system.network?.status);
  const skin = useSelector((state: TStore.State) => state.system.skin);
  const roomAttribute = useSelector((state: TStore.State) => state.room.roomAttribute);
  const isOffline = status === System.NETWORK_STATUS_OFFLINE;
  const isOnline = status === System.NETWORK_STATUS_ONLINE;
  const isLightTheme = skin === NSTheme.SKIN_LIGHT; // 是否为浅色
  const theme = ({ [NSTheme.SKIN_LIGHT]: Light, [NSTheme.SKIN_DARK]: Dark } as Record<NSTheme.Skin, NSTheme.Images>)[
    skin
  ];

  const { desk } = getDesk({ roomAttribute });

  return { isOffline, isOnline, isLightTheme, theme, desk };
};
