import { Desk } from '../basis/types/desk';
import meetingDesk from '../basis/desk/meeting';
import liveDesk from '../basis/desk/live';
import { Meet } from '../basis/types/meet';

export const getDesk = (options: { roomAttribute?: Meet.RoomAttribute } = {}) => {
  const desk = (
    {
      [Meet.ROOMATTRIBUTE_MEETING]: meetingDesk,
      [Meet.ROOMATTRIBUTE_LIVE]: liveDesk,
    } as Record<Meet.RoomAttribute, Desk.Text>
  )[options?.roomAttribute || Meet.ROOMATTRIBUTE_MEETING];

  return { desk };
};
