import { NSTheme } from './theme';
import { Personnel } from '@/common/basis/types/personnel';

/**
 * 会议房间类型
 * @description https://a2y0i9m1jo.feishu.cn/docx/IytndeopFocVZnxNf7scXmCInwR
 */
export namespace Meet {
  // eslint 会管能力类型
  export type Capability = {
    colorTheme: NSTheme.Skin; // 系统主题
    recordSupport: boolean; // 录制能力
    speechToTextSupport: boolean; // 转文字能力
    noiseCancelation: boolean; // 智能降噪能力
    demoDuration: number; // demo时长 单位分钟
    serverTime: number; // 系统时间
  };
  export const TYPE_AUDIO = 'audio';
  export const TYPE_VIDEO = 'video';

  export const MODE_SINGLE = 'single';
  export const MODE_MULTIPLE = 'multiple';

  export const MEETING_TYPE_CALL = 'call';
  export const MEETING_TYPE_MEETING = 'meeting';
  export const MEETING_TYPE_BOOKING = 'booking';

  export const AUTO_MUTE_OPEN = 'open';
  export const AUTO_MUTE_CLOSE = 'close';
  export const AUTO_MUTE_EXCEED = 'exceed';

  export const REPEAT_TYPE_NONE = 'none';
  export const REPEAT_TYPE_DAY = 'day';
  export const REPEAT_TYPE_WEEK = 'week';
  export const REPEAT_TYPE_MONTH = 'month';
  export const REPEAT_TYPE_YEAR = 'year';
  export const REPEAT_TYPE_WORK = 'work';

  export const CANCEL_REASON_1001 = '0x1001';
  export const CANCEL_REASON_1002 = '0x1002';
  export const CANCEL_REASON_1003 = '0x1003';
  export const CANCEL_REASON_1004 = '0x1004';
  export const CANCEL_REASON_1005 = '0x1005';
  export const CANCEL_REASON_1009 = '0x1009';

  // eslint 会议类型语音/视频 audio/video 默认会议是视频类型
  export type MeetType = typeof TYPE_AUDIO | typeof TYPE_VIDEO;

  // 模式单人/多人 single/multiple
  export type Mode = typeof MODE_SINGLE | typeof MODE_MULTIPLE;

  // 会议类型 call(通话) meeting: 会议 booking:（预定）
  export type MeetingType = typeof MEETING_TYPE_CALL | typeof MEETING_TYPE_MEETING | typeof MEETING_TYPE_BOOKING;

  // 入会自动静音 open  close exceed
  export type AutoMute = typeof AUTO_MUTE_OPEN | typeof AUTO_MUTE_CLOSE | typeof AUTO_MUTE_EXCEED;

  // 重复类型 none:不重复 day:每天重复 week:每周重复 month:每月重复 year:每年重复 work:每工作日
  export type RepeatType =
    | typeof REPEAT_TYPE_NONE
    | typeof REPEAT_TYPE_DAY
    | typeof REPEAT_TYPE_WEEK
    | typeof REPEAT_TYPE_MONTH
    | typeof REPEAT_TYPE_YEAR
    | typeof REPEAT_TYPE_WORK;

  // 0x1001: 主叫取消 0x1002: 被叫取消 0x1003: 被叫超时 0x1004: 被叫在忙 0x1005: 通话中断  0x1009:结束会议
  export type CancelReason =
    | typeof CANCEL_REASON_1001
    | typeof CANCEL_REASON_1002
    | typeof CANCEL_REASON_1003
    | typeof CANCEL_REASON_1004
    | typeof CANCEL_REASON_1005
    | typeof CANCEL_REASON_1009;

  export const STATUS_CREATE = 1; // 已创建
  export const STATUS_DOING = 2; // 会议进行中
  export const STATUS_CLOSE = 3; // 房间已关闭

  export const ROOMATTRIBUTE_MEETING = 'meeting'; // 会议
  export const ROOMATTRIBUTE_LIVE = 'live'; // 直播
  export type RoomAttribute = typeof ROOMATTRIBUTE_LIVE | typeof ROOMATTRIBUTE_MEETING;

  // 会议房间
  export type Room = Partial<{
    roomId: string; // 会议房间号
    roomName: string; // 会议名称
    meetType: MeetType; // 会议类型
    mode: Mode;
    initialMode: string; // 第一次: 原始模式
    defaultHost: string; // 默认主持人(值为userid)
    host: string; // 会议主持人(值为userid)
    hostId: string; // 会议主持人(值为userid)
    password: string; // 密码
    key: string; // 密钥
    meetingType: MeetingType;
    autoMute: AutoMute;
    exceedNum: number; // 超过人数 auto_mute为Exceed时需要
    repeatType: RepeatType;
    startedTime: number; // 会议开始时间
    createTime: number; // 创建时间
    startTime: number; // 开始时间 (预约场景)
    stopTime: number; // 结束时间
    duration: number; //  会议时长
    scheduleDefId: number; // 对接日程Id
    status: typeof STATUS_CREATE | typeof STATUS_DOING | typeof STATUS_CLOSE; // 会议房间状态
    cancelReason: CancelReason;
    isRecord: boolean; // 是否录制
    isMuteAll: boolean; // 是否全员静音
    meethost: string; // 会议访问地址 例如: meeting.minxing365.com
    token: string; // 认证信息
    nonce: string; // 秘钥
    meetIntranetUrl: string; // 内网会议地址
    meeturl: string; // 会议地址 例如: wss://meeting.minxing365.com:443
    webUrl: string;
    participants: Personnel.User[];
    roomAttribute: RoomAttribute;
  }>;

  // 会议状态
  export type Status = {
    roomId: string; // 房间号
    roomSid: string; // sid
    startedTime: number; // 开始时间
    finishedTime: number; // 完成时间 当前会议完成时间
    maxParticipant: number; // 最大人数
    shareScreen: string; // 共享屏幕人 （值userId）
    egressId: string; // 录制Id
    muteAll: boolean; // 是否全员静音
  };

  // 用户加入的房间类型
  export type JoinRoom = {
    autoMute: AutoMute;
    hostId: string;
    isMuteAll: boolean;
    hidden: boolean;
    isRecord: boolean;
    meetIntranetUrl: string;
    meetType: MeetType;
    meeturl: string;
    mode: Mode;
    nonce: string;
    password: string;
    roomName: string;
    token: string;
    userId: string;
    startedTime: number;
    webUrl: string; // 会议链接
  };

  export type Timer = Partial<{
    duration: number;
    timerId: NodeJS.Timeout | null;
  }>;

  export type Reconnection = Partial<{
    reconnectCount: number; // 重连次数
    isReconnecting: boolean; // 是否重连中
  }>;
}
